.site-list {
  display: flex;
  flex-direction: column;
  list-style: none;
  padding: 0;
  margin: 0;
  background-color: #81b3d2;
}
.site-list__item {
  color: #ffffff;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 40px;
  font-weight: bold;
  text-transform: uppercase;
  order: 0;
  border-bottom: 1px solid rgba(103, 143, 168, 0.39);
  /*border-top: 1px solid rgba(154, 194, 219, 0.3);*/
  text-align: center;
  cursor: pointer;
}
.site-list__item--panel {
  background-color: #ffffff;
  order: 1;
  padding-left: 109px;
  padding-right: 109px;
}
.site-list__item:hover {
  background-color: #669ec0;
}
.site-list__item:active {
  background-color: #5496bd;
}
.site-list__item-link {
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  margin: 0 auto;
}
.site-list__item-link:active {
  background-color: #5496bd;
  opacity: 0.3;
}
.main-nav {
  position: relative;
  width: 320px;
  margin: 0 auto;
}
.main-nav__toggle {
  position: absolute;
  bottom: 17px;
  right: 20px;
  display: block;
  width: 23px;
  height: 22px;
  cursor: pointer;
  border: none;
  padding: 0;
  font-size: 0;
  line-height: 0;
  background-color: #ffffff;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2;
}
.main-nav--opened .main-nav__toggle {
  top: 17px;
  right: 20px;
  background-color: #81b3d2;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  top: 9px;
  width: 25px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--closed .user-list__item {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
  .site-list__item-link {
    display: block;
    width: 320px;
    padding-top: 7px;
    padding-bottom: 7px;
  }
  .site-list__item-link:active {
    background-color: #5496bd;
    opacity: 0.3;
  }
}
@media (min-width: 768px) {
  .main-nav {
    /*Временно для сетки*/
    min-height: 56px;
    background-color: #ffffff;
    width: auto;
    margin: 0;
  }
  .main-nav__wrapper {
    width: 768px;
    margin: 0 auto;
  }
  .main-nav__toggle {
    display: none;
  }
  .site-list {
    display: flex;
    flex-direction: row;
    width: 768px;
    background-color: #ffffff;
  }
  .site-list__item {
    width: 153px;
    border: none;
    margin-top: 7px;
  }
  .site-list__item--panel {
    width: 102px;
    padding: 0;
    margin: 0 27px;
    order: 0;
  }
  .site-list__item:hover {
    background-color: #ffffff;
  }
  .site-list__item-link {
    color: #000000;
    font-size: 14px;
    line-height: 26px;
  }
  .site-list__item-link--active {
    color: #766357;
    cursor: default;
  }
  .site-list__item-link--active:hover {
    color: #604e43;
    background-color: #ffffff;
  }
  .site-list__item-link--active:active {
    color: #766357;
    opacity: 0.3;
    background-color: #ffffff;
  }
  .site-list__item-link[href]:hover {
    color: #81b3d2;
    background-color: #ffffff;
  }
  .site-list__item-link[href]:active {
